/* @import url(''); */

*{
    margin: 0;
    padding: 0;
}


body{
    /* background-color: #333; */
    /* color: #fff; */
    font-family: "Montserrat Alternates", serif;
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 32px;
    color: #0B0B15;
}

.header{
    background-color: black;
    padding: 20px;
}

.main{
    margin: 50px, 100px;
    width: 50%;
    float: left;
}

.main .lecture h3{
    color: #0B0B15;
    padding: 20% auto 20% 20%;
}

.main > .lecture:nth-child(2n+1){
width: 100%;
height: auto;
background: linear-gradient(274.01deg, #C9DBBF 19.44%, #95BCC5 89.33%);

}
.main .lectures h3:nth-child(2n){
    width: 100%;
    height: auto;
}

section{
    background-color: #000000;
}

